import { FC, memo } from "react";
import { Flex, Image, Select } from "@chakra-ui/react";
import { countries } from "../../constants/countries";
import { nanoid } from "nanoid";
import { CountrySelectorProps } from "../../interfaces";

const CountrySelector: FC<CountrySelectorProps> = ({
  value,
  onChange,
  isCallingCode,
  disabled,
  ...rest
}) => {
  const { root } = useStyles();

  const countryData = countries?.map((data) => {
    return { id: nanoid(), ...data };
  });

  const flag = countryData.filter(
    (e) => e.name === value || e.callingCodes[0] === value
  )?.[0];
  return (
    <Flex {...root} {...rest}>
      <Image
        data-testid={"img-1"}
        src={flag?.flag}
        style={{ width: "28px", height: "28px", borderRadius: "20px" }}
      />
      <Select
        onChange={onChange}
        value={value}
        variant="unstyled"
        placeholder="Select country..."
        pl={"5px"}
        h="48px"
        alignSelf="center"
        justifySelf={"center"}
        justifyContent="center"
        disabled={disabled}

        // marginBottom="10px"
      >
        {countryData
          .sort(function (a: any, b: any) {
            return b.callingCodes[0] - a.callingCodes[0];
          })
          ?.map((e: any) => (
            <option
              key={nanoid()}
              value={isCallingCode ? e.callingCodes[0] : e.name}
            >
              {isCallingCode ? "" : e.name}
            </option>
          ))}
      </Select>
    </Flex>
  );
};

export default memo(CountrySelector);

const useStyles = () => {
  return {
    root: {
      w: "100%",
      h: "48px",
      align: "center",
      justify: "center",
      borderRadius: 58,
      px: "10px",
      bg: "grey",
      outline: "none",
      boxShadow: "0px",
    },
  };
};
